
// @ is an alias to /src
import { defineComponent } from "vue";

export default defineComponent({
	name: 'BlankType',
	props: {
		source: {
			type: String,
			required: true
		}
	},
	data() {
		return {
			countdown: 2
		}
	},
	mounted() {
		this.countdownTimer();
	},
	methods: {
		countdownTimer() {
			if(this.countdown > 0) {
				setTimeout(() => {
					this.countdown -= 1;
					this.countdownTimer();
				}, 1000);
				return;
			}
			
			window.open(this.source);
		}
	}
})
